/* purgecss start ignore */

.lc-pagination .MuiPaginationItem-root, [class*='lc-pagination-'] .MuiPaginationItem-root {
    font-family: var(--lc-font-family-sans);
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-black), var(--tw-text-opacity));
    font-size: 16px;
    line-height: 1.5;
  }

.lc-pagination .MuiPaginationItem-root:hover, [class*='lc-pagination-'] .MuiPaginationItem-root:hover {
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-gray-lighter), var(--tw-bg-opacity));
    }

.lc-pagination .MuiPaginationItem-page.Mui-selected, [class*='lc-pagination-'] .MuiPaginationItem-page.Mui-selected {
    font-weight: 700;
  }

.lc-pagination-primary .MuiPaginationItem-page.Mui-selected, .lc-pagination-accent .MuiPaginationItem-page.Mui-selected {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-white), var(--tw-text-opacity));
      border-style: none;
    }

.lc-pagination-primary .MuiPaginationItem-page.Mui-selected {
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-primary), var(--tw-bg-opacity));
  }

.lc-pagination-primary .MuiPaginationItem-page.Mui-selected:hover {
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-primary-dark), var(--tw-bg-opacity));
    }

.lc-pagination-accent .MuiPaginationItem-page.Mui-selected {
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-accent), var(--tw-bg-opacity));
  }

.lc-pagination-accent .MuiPaginationItem-page.Mui-selected:hover {
      --tw-bg-opacity: 1;
      background-color: rgba(var(--lc-color-accent-dark), var(--tw-bg-opacity));
    }

.lc-pagination-outline-primary .MuiPaginationItem-page.Mui-selected {
    background-color: rgba(var(--lc-color-primary), 0.2);
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-primary), var(--tw-text-opacity));
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-primary), var(--tw-border-opacity));
  }

.lc-pagination-outline-primary .MuiPaginationItem-page.Mui-selected:hover {
      background-color: rgba(var(--lc-color-primary-dark), 0.2);
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-primary-dark), var(--tw-border-opacity));
    }

.lc-pagination-outline-accent .MuiPaginationItem-page.Mui-selected {
    background-color: rgba(var(--lc-color-accent), 0.3);
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-white), var(--tw-text-opacity));
    --tw-border-opacity: 1;
    border-color: rgba(var(--lc-color-accent), var(--tw-border-opacity));
  }

.lc-pagination-outline-accent .MuiPaginationItem-page.Mui-selected:hover {
      background-color: rgba(var(--lc-color-accent-dark), 0.3);
      --tw-border-opacity: 1;
      border-color: rgba(var(--lc-color-accent-dark), var(--tw-border-opacity));
    }

/* purgecss end ignore */
